@import './constants';
@import 'animations/bounce';
@import 'animations/slide';

$rt-namespace: 'Toastify';

.#{$rt-namespace}__toast-container {
  position: fixed;
  -webkit-transform: translate3d(0, 0, var(--sb-toast-z-index-px, 9999px));
  transform: translate3d(0, 0, var(--sb-toast-z-index-px, 9999px));
  width: var(--sb-toast-width, 320px);
  z-index: var(--sb-toast-z-index, var(--sbt-stack-max));

  &.full-width {
    display: flex;
    flex-direction: column;
    width: calc(100% - 2em);
  }

  &.inline {
    position: relative;
    width: unset;
  }
}

.#{$rt-namespace}__toast-container--top-left {
  left: 1em;
  top: 1em;

  &.inline {
    left: unset;
    top: unset;
  }
}

.#{$rt-namespace}__toast-container--top-center {
  left: 50%;
  margin-left: var(--sb-toast-neg-half-width, -160px);
  top: 1em;

  &.inline {
    left: unset;
    margin-left: unset;
    top: unset;
  }

  &.full-width {
    align-items: center;
    left: 1rem;
    margin-left: unset;
  }
}

.#{$rt-namespace}__toast-container--top-right {
  right: 1em;
  top: 1em;

  &.inline {
    right: unset;
    top: unset;
  }
}

.#{$rt-namespace}__toast-container--bottom-left {
  bottom: 1em;
  left: 1em;

  &.inline {
    bottom: unset;
    left: unset;
  }
}

.#{$rt-namespace}__toast-container--bottom-center {
  bottom: 1em;
  left: 50%;
  margin-left: var(--sb-toast-neg-half-width, -160px);

  &.inline {
    bottom: unset;
    left: unset;
    margin-left: unset;
  }

  &.full-width {
    align-items: center;
    left: 1rem;
    margin-left: unset;
  }
}

.#{$rt-namespace}__toast-container--bottom-right {
  bottom: 1em;
  right: 1em;

  &.inline {
    bottom: unset;
    right: unset;
  }

  &.full-width {
    align-items: flex-end;
  }
}

@media #{$rt-mobile} {
  .#{$rt-namespace}__toast-container {
    left: 0;
    margin: 0;
    padding: 0;
    width: 100vw;
  }

  .#{$rt-namespace}__toast-container--top-left,
  .#{$rt-namespace}__toast-container--top-center,
  .#{$rt-namespace}__toast-container--top-right {
    top: 0;
  }
  .#{$rt-namespace}__toast-container--bottom-left,
  .#{$rt-namespace}__toast-container--bottom-center,
  .#{$rt-namespace}__toast-container--bottom-right {
    bottom: 0;
  }
  .#{$rt-namespace}__toast-container--rtl {
    left: initial;
    right: 0;
  }
}

@keyframes #{$rt-namespace}__trackProgress {
  0% {
    transform: scaleX(1);
  }

  100% {
    transform: scaleX(0);
  }
}

.#{$rt-namespace}__progress-bar {
  background-color: rgba(255, 255, 255, 0.7);
  bottom: 0;
  height: 5px;
  left: 0;
  opacity: 0.7;
  position: absolute;
  transform-origin: left;
  width: 100%;
  z-index: var(--sb-toast-z-index, var(--sbt-stack-max));
}

.#{$rt-namespace}__progress-bar--animated {
  animation: #{$rt-namespace}__trackProgress linear 1 forwards;
}

.#{$rt-namespace}__progress-bar--controlled {
  transition: transform 0.2s;
}

.#{$rt-namespace}__progress-bar--rtl {
  left: initial;
  right: 0;
  transform-origin: right;
}
